import React, { useState } from "react";
import { signInWithEmailAndPassword } from "firebase/auth";
import { auth } from "./firebase";
import { Eye, EyeOff } from 'lucide-react'; // Removed the extra closing tags at the end of the file
const AdminLogin = ({ onLogin, onLoginError }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");
  const [showPassword, setShowPassword] = useState(false);

  const handleLogin = async (e) => {
    e.preventDefault();
    setError("");
    try {
      await signInWithEmailAndPassword(auth, email, password);
      // notify parent (App) that login happened
      if (onLogin) onLogin();
      setEmail("");
      setPassword("");
    } catch (err) {
      console.error("Login error:", err);
      const errorMessage = err.code === 'auth/invalid-credential' 
        ? "Invalid email or password." 
        : err.message || "Login failed";
      setError(errorMessage);
      if (onLoginError) onLoginError(errorMessage);
    }
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100">
      <form onSubmit={handleLogin} className="w-full max-w-md p-8 space-y-6 bg-white rounded-lg shadow-lg">
        <h3 className="text-2xl font-bold text-center text-gray-800">Admin Login</h3>

        <div>
          <label className="block mb-2 text-sm font-medium text-gray-600">Email</label>
          <input
            type="email"
            placeholder="admin@yourdomain.com"
            value={email}
            onChange={(e) => {
              setEmail(e.target.value);
              setError("");
            }}
            className="w-full px-4 py-3 text-gray-800 bg-gray-50 border-2 border-gray-200 rounded-lg focus:ring-blue-500 focus:border-blue-500 transition-smooth"
            required
            autoComplete="username"
          />
        </div>

        <div>
          <label className="block mb-2 text-sm font-medium text-gray-600">Password</label>
          <div className="relative">
            <input
              type={showPassword ? "text" : "password"}
              placeholder="Password"
              value={password}
              onChange={(e) => {
                setPassword(e.target.value);
                setError("");
              }}
              className="w-full px-4 py-3 text-gray-800 bg-gray-50 border-2 border-gray-200 rounded-lg focus:ring-blue-500 focus:border-blue-500 transition-smooth"
              required
              autoComplete="current-password"
            />
            <button
              type="button"
              onClick={() => setShowPassword(!showPassword)}
              className="absolute inset-y-0 right-0 flex items-center px-4 text-gray-500 hover:text-gray-700"
            >
              {showPassword ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
            </button>
          </div>
        </div>

        {error && <p className="text-red-600 text-sm text-center font-semibold">{error}</p>}

        <div>
          <button type="submit" className="w-full px-4 py-3 font-semibold text-white bg-blue-600 rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-smooth active:scale-95">
            Sign in
          </button>
        </div>
      </form>
    </div>
  );
};

export default AdminLogin;
